@content0: content0;

.@{content0}-wrapper {
  min-height: 446px;
  overflow: hidden;
  background-color: #000;

  .@{content0} {
    height: 100%;
    padding-top: 96px;
	padding-bottom: 0px;
    
	>.title-wrapper {
      margin: 0;
	  >h1 {
        font-size: 36px;
	    color: #d89c31;
		margin-bottom: 0;
	  }
    }

    &-block {
      padding: 0;
      display: inline-block;
      text-align: center;
      min-height: 300px;
	  margin-bottom: 0px;
      
	  img {
	  margin-top: 20px;
        width: 100%;
      }

      &-wrapper {
        position: relative;
        height: 100%;
        top: 25%;
        padding: 10px;
      }

      &.queue-anim-leaving {
        position: relative !important;
      }

      &-icon {
        width: 240px;
        height: 240px;
        margin: auto;
		margin-bottom: 0;
		padding-bottom: 0;
      }

      &-title {
	    color: #fff;
        line-height: 32px;
        margin: 0px auto;
	    margin-top: 0px;
	    margin-bottom: 0px;
		font-size: 27px;
      }
	  
    }
  }
}

@media screen and (max-width: 767px) {
  .@{content0}-wrapper {
    min-height: 700px;
	
	.@{content0} {
	
      padding-top: 64px;
	  >.title-wrapper {
	    >h1 {
          font-size: 32px;
	    }
      } 
	  &-title {
	    font-size: 24px;
	  }
	}
  }
}
