@content1: content1;

.@{content1}-wrapper {
  margin-top: 64px;
  background-color: #000;
  .@{content1} {
    padding: 0 24px;
	
	&-img {
	  margin: 0 auto;
	  width: 50%;
	}
	
    &-text {
	  height: auto;
	  text-align: center;
	  margin-top: 0px;
      padding: 0 32px;
      .@{content1}-content {
	  	color: #fff;
	    width: 100%;
		margin-top: 40px;
		margin-bottom: 40px;
		line-height: 24px;
      }
      .@{content1}-title {
	    color: #d89c31;
        margin: 32px auto 16px;
	    line-height: 32px;
        font-size: 36px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .@{content1}-wrapper {
    .@{content1} {
		
	  &-img {
	    width: 100%;
	  }
	  
      &-text {
        height: 550px;
        margin-bottom: 0px;
        text-align: center;
        padding: 0;
        .@{content1}-content,
        .@{content1}-title {
          width: 100%;
          top: auto;
        }
        .@{content1}-title {
          margin: 32px auto 16px;
          font-size: 32px;
        }
      }
    }
  }
}
