@content2: content2;

.@{content2}-wrapper {
  min-height: 446px;
  overflow: hidden;
  background-color: #000;
  text-align: center;

  .@{content2} {
    height: 100%;
    padding-top: 24px;

    >.title-wrapper {
      margin: 0;
	  >h1 {
		font-size: 36px;
	    color: #d89c31;
		margin-bottom: 0;
	  }
    }

    &-block {
      padding: 0;
      display: inline-block;
      text-align: center;
      min-height: 200px;
      img {
        width: 100%;
      }

      &-wrapper {
        position: relative;
        height: 100%;
        top: 25%;
        padding: 10px;
      }

      &.queue-anim-leaving {
        position: relative !important;
      }

      &-icon {
        width: 240px;
        height: 240px;
        margin: auto;
		
      }

      &-title {
	    color: #fff;
        line-height: 36px;
        margin: 0px auto;
	    margin-top: 0px;
		font-size: 27px;
      }
    }
	
    &-content {
	  margin-top: 30px;
	  color: #fff;
    }
  }
}

@media screen and (max-width: 767px) {
  .@{content2}-wrapper {
    min-height: 720px;
	.@{content2} {
      padding-top: 64px;
	
	  >.title-wrapper {
	    >h1 {
          font-size: 32px;
	    }
      } 
	  &-title {
	    font-size: 24px;
	  }
    }
  }
}
