
.faq-title {
  margin-top: 72px;
  text-align: center;
  color: #fff;
  font-size: 36px;
}

.faq-row-wrapper {
  background-color: #000;
  color: #fff;
  padding:  15px 25px;
  padding-bottom: 90px;
  
  .faq-body {
    .faq-row {
	  border-top: 1px solid #d89c31;
	  border-bottom: 0;
	  line-height: 1.75em;
	  
      .row-content {
        .row-content-text {
	      color: #fff;
        }
      }
    }
  }
}


.icons-footer {
  width: 80px;
  margin-right: 1%;
  bottom: 10%;
  float: right;
  color: #d89c31;
	
  .icon-twitter {
    margin-bottom: 10%;
	color: #d89c31;
  }
  .icon-discord {
    margin-bottom: 10%;
	margin-left: 15%;
	color: #d89c31;
    margin-right: 10%;
  }
}

.logo {
  float: left;
  left: 5%;
  
  .logo-img {
    margin-bottom: 10%;
    padding-left: 20px;
	margin-top: -5%;
    height: 40px;
    width: 180px;
  }
}



@media screen and (max-width: 767px) {
  .faq-title {
    font-size: 32px;
    margin-top: 10px;
  }
  
  .icons-footer {
    margin-right: 3%;
  }
}